<template>
	<view class="page-container">
		<!-- 顶部背景 -->
		<view class="bg-header"></view>

		<view class="enterImg">
			<image src="/static/image/enter.png" mode=""></image>
		</view>

		<!-- 内容区 -->
		<view class="form-container">
			<view class="form-title">请填写信息</view>
			<view class="form-item">
				<view class="item-label">姓名/公司名</view>
				<u-input class="item-input" v-model="formData.nickName" placeholder="请填写姓名/公司名称" :border="false"></u-input>
			</view>
			<view class="form-item">
				<view class="item-label">电话</view>
				<u-input class="item-input" v-model="formData.userName" placeholder="请填写电话" :border="false"
					type="number"></u-input>
			</view>
			<view class="form-item textarea-item">
				<view class="item-label">备注</view>
				<u-input class="item-input" v-model="formData.remark" placeholder="请填写备注" :border="false" type="textarea"></u-input>
			</view>
		</view>


		<!-- 底部按钮 -->
		<!-- 添加占位区域，防止内容被底部footer遮挡 -->
		<view class="footer-placeholder"></view>

		<view class="footer">
			<u-button class="btn" :custom-style="{border: 'none'}" @click="submitForm">
				提交信息
			</u-button>
		</view>
	</view>
</template>

<script>
	import {
		userApi
	} from '../../../api/user.js';
	import settings from '../../../common/settings.js';
	export default {
		data() {
			return {
				formData: {
					nickName: '',
					userName: '',
					remark: '',
				},
			};
		},
		onLoad(options) {
			console.log(options)
		},
		onShow() {
			// 设置导航栏背景色与bg-header起始颜色一致
			uni.setNavigationBarColor({
				frontColor: '#ffffff',
				backgroundColor: '#63B872'
			})
		},
		methods: {
			submitForm() {
				// 表单验证
				if (!this.formData.nickName) {
					this.showToast('请填写姓名/公司名称');
					return;
				}
				
				if (!this.formData.userName) {
					this.showToast('请填写电话');
					return;
				}
				
				// 手机号校验
				const phoneRegex = /^1[3-9]\d{9}$/;
				if (!phoneRegex.test(this.formData.userName)) {
					this.showToast('请输入正确的手机号码');
					return;
				}
				
				// 所有验证通过，提交表单
				uni.showLoading({
					title: '提交中...'
				});
				
				// 调用接口
				userApi.addUser(this.formData).then(res => {
					console.log(res);
					uni.hideLoading();
					
					// 判断接口返回结果
					if (res.code === 200) {
						uni.showToast({
							title: '提交成功',
							icon: 'success'
						});
						
						// 提交成功后，清空表单或跳转页面
						setTimeout(() => {
							// 返回上一页或其他操作
							// uni.navigateBack();
							// 或清空表单
							this.resetForm();
						}, 1500);
					} else {
						this.showToast(res.message || '提交失败，请重试');
					}
				}).catch(err => {
					console.error(err);
					uni.hideLoading();
					this.showToast('提交失败，请检查网络后重试');
				});
			},
			showToast(title) {
				uni.showToast({
					title: title,
					icon: 'none'
				});
			},
			resetForm() {
				this.formData = {
					nickName: '',
					userName: '',
					remark: ''
				};
			}
		}
	}
</script>

<style lang="scss">
	page {
		background-color: #f5f6f9;
	}

	.slogan {
		padding: 40rpx 0 20rpx 40rpx;
		display: flex;
		flex-direction: column;
		color: #357A3E;
		font-size: 32rpx;
		font-weight: 500;
		line-height: 1.6;
	}

	.enterImg {
		width: 100%;
		height: 190rpx;

		image {
			width: 100%;
			height: 190rpx;
		}
	}

	.bg-header {
		left: 0rpx;
		top: -34rpx;
		width: 100%;
		height: 724rpx;
		opacity: 1;
		border-radius: 0rpx 0rpx, 16rpx, 16rpx;
		background: linear-gradient(180deg, rgba(99, 184, 114, 1) 0%, rgba(245, 246, 249, 1) 100%);
		position: absolute;
		z-index: -1;
	}

	.page-container {
		// position: relative;
		padding: 0 30rpx;
	}

	.form-container {
		margin-top: 0rpx;
		background-color: #fff;
		border-radius: 16rpx;
		padding: 30rpx;
		box-shadow: 0 2rpx 10rpx rgba(0, 0, 0, 0.05);
	}

	.form-title {
		font-size: 32rpx;
		font-weight: 500;
		margin-bottom: 30rpx;
		color: #333;
	}

	.form-item {
		display: flex;
		align-items: center;
		padding: 10rpx 0;
		border-bottom: 2rpx solid #f5f6f9;
	}
	
	.textarea-item {
		align-items: flex-start;
		padding: 20rpx 0;
	}

	.item-label {
		width: 160rpx;
		font-size: 28rpx;
		color: #333;
	}

	.item-input {
		flex: 1;
		text-align: right;
	}

	.footer-placeholder {
		height: 130rpx;
	}

	.footer {
		display: flex;
		justify-content: space-between;
		padding: 30rpx 30rpx 60rpx 30rpx;
		position: fixed;
		left: 0;
		right: 0;
		bottom: 0;
		background-color: #fff;
		z-index: 999;
		box-shadow: 0 -2rpx 10rpx rgba(0, 0, 0, 0.05);

		.btn {
			width: 100%;

			::v-deep .u-button {
				border: none !important;
				background: linear-gradient(237.74deg, rgba(114, 186, 112, 1) 0%, rgba(53, 122, 62, 1) 100%);
				height: 88rpx !important;
			}

			button {
				border-radius: 44rpx;
				background: linear-gradient(237.74deg, rgba(114, 186, 112, 1) 0%, rgba(53, 122, 62, 1) 100%);
				border: none !important;
				color: #fff;
				height: 88rpx;
				line-height: 88rpx;
				font-size: 32rpx;
			}
		}
	}
</style>